import React, { useEffect, useState } from "react";
import styled from "styled-components";
import "./styles.css";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import PlazmLogo from "../../../../images/plazm_logo.jpg";
import LocalNav from "../../../../images/LocalNav.png";
import ProfileImg from "../../../../images/profile-img.png";
import Mention from "../../../../images/Mentions.png";
import Notifications from "../../../../images/notifications.png";
import Favorites from "../../../../images/Left-favourite.png";
import GridIcon from "../../../../images/Grid_icon.png";
import ProfileSettingImg from "../../../../images/Profile_Setting.png";
import { BiSearchAlt2 } from "react-icons/bi";
import ChangePassword from "../../../Consumer/ChangePassword";
import ProfileSettings from "../../../Consumer/ProfileSettings";
import BuisinessView from "../../../Consumer/BuisinessView";
import BusinessList from "../../../Consumer/BusinessList";
import { useDispatch, useSelector } from "react-redux";
import ListOptionView from "../../../Consumer/ListOptionView";
import ListDescriptionView from "../../../Consumer/ListDescriptionView";
import MyFeed from "../../../Consumer/MyFeed";
import {
  clearMyFeedData,
  fetchMyFeedData,
  HomeSearch,
} from "../../../../reducers/myFeedReducer";
import Profile from "../../../Consumer/Profile";
import HomeSearchComponent from "../../../Consumer/HomeSearch";

const LeftBarContent = styled.div`
  width: 100px;
  position: relative;
  display: flex;
  /* background: #F3F3F3; */
  box-shadow: 0px 4px 8px rgba(44, 39, 56, 0.04);
  @media (max-width: 767px) {
    width: 50px;
  }
`;

const UserImage = styled.div`
  width: 50px;
  height: 50px;
  border: 3px solid #ff2a88;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  @media (max-width: 767px) {
    width: 30px;
    height: 30px;
  }
`;

const SearchIcon = styled.div`
  width: 36px;
  svg {
    color: #767676;
    font-size: 38px;
    @media (max-width: 767px) {
      font-size: 30px;
    }
  }
`;

const LeftBar = ({
  profile,
  setFlag,
  isBusinessOpen,
  businessExists,
  businessId,
  isUserOpen,
  userId,
}) => {
  const [displayChangePassword, setDisplayChangePassword] = useState(false);
  const [tabIndex, setTabIndex] = useState(
    isBusinessOpen ? 6 : isUserOpen ? 3 : 0
  );
  const user = useSelector((state) => state.user.user);
  const [selectedListId, setSelectedListId] = useState(null);
  const [listClickedFromSearch, setListClickedFromSearch] = useState(false);
  const loading = useSelector((state) => state.myFeed.loading);
  const [searchIndex, setSearchIndex] = useState(null);
  const [myFeedIndex, setMyFeedIndex] = useState(null);
  const [listIndex, setListIndex] = useState(null);
  const [favoriteIndex, setFavoriteIndex] = useState(null);
  const dispatch = useDispatch();

  useEffect(() => {
    if (selectedListId !== null) setTabIndex(7);
  }, [selectedListId]);

  /** to clear selected data on tab click */
  const listView = () => {
    if (tabIndex !== 7 && !loading) {
      dispatch(clearMyFeedData());
      setSelectedListId(null);
      setListIndex(null);
    }
  };

  /** to clear selected data on tab click */
  const myFeedFunction = () => {
    if (tabIndex !== 4 && !loading) {
      const obj = {
        id: user._id,
        value: 0,
      };
      dispatch(clearMyFeedData());
      dispatch(fetchMyFeedData(obj));
    }
  };

  /** to clear selected data on tab click */
  const homeSearchFunction = () => {
    setFavoriteIndex(null)
    if (tabIndex !== 3 && !loading) {
      const obj = {
        search: "",
        value: 0,
        filters: { closest: false, updated: false },
        latitude: process.env.REACT_APP_LATITUDE,
        longitude: process.env.REACT_APP_LONGITUDE,
      };
      dispatch(clearMyFeedData());
      dispatch(HomeSearch(obj));
    }
  };
  return (
    <>
      <LeftBarContent className="MainTabs">
        <Tabs selectedIndex={tabIndex} onSelect={(index) => setTabIndex(index)}>
          <TabList>
            <Tab
              disabled={loading || tabIndex === 0}
              className={
                0 === tabIndex - 1
                  ? "react-tabs__tab LIBefore"
                  : tabIndex + 1 === 0
                  ? "react-tabs__tab LIAFter"
                  : tabIndex === 0
                  ? "react-tabs__tab react-tabs__tab--selected"
                  : "react-tabs__tab"
              }
            >
              <img src={PlazmLogo} alt="" />
            </Tab>
            <Tab
              disabled={loading || tabIndex === 1}
              className={
                1 === tabIndex - 1
                  ? "react-tabs__tab LIBefore"
                  : tabIndex === 1
                  ? "react-tabs__tab react-tabs__tab--selected"
                  : "react-tabs__tab"
              }
            >
              <img src={LocalNav} alt="" />
            </Tab>
            <Tab
              disabled={loading || tabIndex === 2}
              className={
                2 === tabIndex - 1
                  ? "react-tabs__tab LIBefore"
                  : tabIndex + 1 === 2
                  ? "react-tabs__tab LIAFter"
                  : tabIndex === 2
                  ? "react-tabs__tab react-tabs__tab--selected"
                  : "react-tabs__tab"
              }
            >
              <UserImage>
                <img src={user.photo ? user.photo : ProfileImg} alt="" />
              </UserImage>
            </Tab>
            <Tab
              disabled={loading || tabIndex === 3}
              className={
                3 === tabIndex - 1
                  ? "react-tabs__tab LIBefore"
                  : tabIndex + 1 === 3
                  ? "react-tabs__tab LIAFter"
                  : tabIndex === 3
                  ? "react-tabs__tab react-tabs__tab--selected"
                  : "react-tabs__tab"
              }
              onClick={() => homeSearchFunction()}
            >
              <SearchIcon>
                <BiSearchAlt2 />
              </SearchIcon>
            </Tab>
            <Tab
              disabled={loading || tabIndex === 4}
              className={
                4 === tabIndex - 1
                  ? "react-tabs__tab LIBefore"
                  : tabIndex + 1 === 4
                  ? "react-tabs__tab LIAFter"
                  : tabIndex === 4
                  ? "react-tabs__tab react-tabs__tab--selected"
                  : "react-tabs__tab"
              }
              onClick={() => myFeedFunction()}
            >
              <img src={Mention} alt="" />
            </Tab>
            <Tab
              disabled={loading || tabIndex === 5}
              className={
                5 === tabIndex - 1
                  ? "react-tabs__tab LIBefore"
                  : tabIndex + 1 === 5
                  ? "react-tabs__tab LIAFter"
                  : tabIndex === 5
                  ? "react-tabs__tab react-tabs__tab--selected"
                  : "react-tabs__tab"
              }
            >
              <img src={Notifications} alt="" />
            </Tab>
            <Tab
              disabled={loading || tabIndex === 6}
              className={
                6 === tabIndex - 1
                  ? "react-tabs__tab LIBefore"
                  : tabIndex + 1 === 6
                  ? "react-tabs__tab LIAFter"
                  : tabIndex === 6
                  ? "react-tabs__tab react-tabs__tab--selected"
                  : "react-tabs__tab"
              }
            >
              <img src={Favorites} alt="" />
            </Tab>
            <Tab
              disabled={loading || tabIndex === 7}
              className={
                7 === tabIndex - 1
                  ? "react-tabs__tab LIBefore"
                  : tabIndex + 1 === 7
                  ? "react-tabs__tab LIAFter"
                  : tabIndex === 7
                  ? "react-tabs__tab react-tabs__tab--selected"
                  : "react-tabs__tab"
              }
              onClick={() => listView()}
            >
              <img src={GridIcon} alt="" />
            </Tab>
            <Tab
              disabled={loading || tabIndex === 8}
              className={
                8 === tabIndex - 1
                  ? "react-tabs__tab LIBefore"
                  : tabIndex + 1 === 8
                  ? "react-tabs__tab LIAFter"
                  : tabIndex === 8
                  ? "react-tabs__tab react-tabs__tab--selected"
                  : "react-tabs__tab"
              }
            >
              <img src={ProfileSettingImg} alt="" />
            </Tab>

            <Tab
              disabled={loading}
              className={
                10 === tabIndex - 1
                  ? "react-tabs__tab LIBefore"
                  : tabIndex + 1 === 10
                  ? "react-tabs__tab LIAFter"
                  : tabIndex === 10
                  ? "react-tabs__tab react-tabs__tab--selected"
                  : "react-tabs__tab"
              }
            >
              &nbsp;
            </Tab>
            <Tab
              disabled={loading}
              className={
                11 === tabIndex - 1
                  ? "react-tabs__tab LIBefore"
                  : tabIndex + 1 === 11
                  ? "react-tabs__tab LIAFter"
                  : tabIndex === 11
                  ? "react-tabs__tab react-tabs__tab--selected"
                  : "react-tabs__tab"
              }
            >
              &nbsp;
            </Tab>
          </TabList>

          <TabPanel></TabPanel>
          <TabPanel>
            <div className="panel-content">
              <h2>Any content 2</h2>
            </div>
          </TabPanel>
          <TabPanel>
            <div className="panel-content">
              <h2>Any content 3</h2>
            </div>
          </TabPanel>
          <TabPanel>
            <div className="panel-content">
              {isUserOpen ? (
                <Profile setDisplayTab={() => setTabIndex(0)} userId={userId} />
              ) : !searchIndex ? (
                <HomeSearchComponent
                  setDisplayTab={() => setTabIndex(0)}
                  setSelectedListId={setSelectedListId}
                  setListClickedFromSearch={setListClickedFromSearch}
                  setSearchIndex={setSearchIndex}
                />
              ) : (
                <BuisinessView
                  setDisplayTab={() => setTabIndex(0)}
                  profile={profile}
                  businessExists={businessExists}
                  businessId={businessId}
                  searchIndex={searchIndex}
                  setTabIndex={setTabIndex}
                  setSearchIndex={setSearchIndex}
                  myFeedIndex={myFeedIndex}
                  setMyFeedIndex={setMyFeedIndex}
                  setListIndex={setListIndex}
                  listIndex={listIndex}
                  favoriteIndex={favoriteIndex}
                  setFavoriteIndex={setFavoriteIndex}
                />
              )}
            </div>
          </TabPanel>
          <TabPanel>
            <div className="panel-content">
              {!myFeedIndex ? (
                <MyFeed
                  setDisplayTab={() => setTabIndex(0)}
                  setMyFeedIndex={setMyFeedIndex}
                />
              ) : (
                <BuisinessView
                  setDisplayTab={() => setTabIndex(0)}
                  profile={profile}
                  businessExists={businessExists}
                  businessId={businessId}
                  searchIndex={searchIndex}
                  setTabIndex={setTabIndex}
                  setSearchIndex={setSearchIndex}
                  myFeedIndex={myFeedIndex}
                  setMyFeedIndex={setMyFeedIndex}
                  setListIndex={setListIndex}
                  listIndex={listIndex}
                  favoriteIndex={favoriteIndex}
                  setFavoriteIndex={setFavoriteIndex}
                />
              )}
            </div>
          </TabPanel>
          <TabPanel>
            <div className="panel-content">
              <h2>Any content 6</h2>
            </div>
          </TabPanel>
          <TabPanel>
            <div className="panel-content">
              {favoriteIndex ? (
                <BuisinessView
                  setDisplayTab={() => setTabIndex(0)}
                  profile={profile}
                  businessExists={businessExists}
                  businessId={businessId}
                  searchIndex={searchIndex}
                  setTabIndex={setTabIndex}
                  setSearchIndex={setSearchIndex}
                  myFeedIndex={myFeedIndex}
                  setMyFeedIndex={setMyFeedIndex}
                  setListIndex={setListIndex}
                  listIndex={listIndex}
                  favoriteIndex={favoriteIndex}
                  setFavoriteIndex={setFavoriteIndex}
                />
              ) : (
                <BusinessList setDisplayTab={() => setTabIndex(0)} setFavoriteIndex={setFavoriteIndex}/>
              )}
            </div>
          </TabPanel>
          <TabPanel>
            <div className="panel-content">
              {!selectedListId ? (
                <ListOptionView
                  setDisplayTab={() => setTabIndex(0)}
                  setSelectedListId={setSelectedListId}
                  selectedListId={selectedListId}
                />
              ) : !listIndex ? (
                <ListDescriptionView
                  setDisplayTab={() => setTabIndex(0)}
                  setSelectedListId={setSelectedListId}
                  selectedListId={selectedListId}
                  listClickedFromSearch={listClickedFromSearch}
                  setListClickedFromSearch={setListClickedFromSearch}
                  setListIndex={setListIndex}
                />
              ) : (
                <BuisinessView
                  setDisplayTab={() => setTabIndex(0)}
                  profile={profile}
                  businessExists={businessExists}
                  businessId={businessId}
                  searchIndex={searchIndex}
                  setTabIndex={setTabIndex}
                  setSearchIndex={setSearchIndex}
                  myFeedIndex={myFeedIndex}
                  setMyFeedIndex={setMyFeedIndex}
                  setListIndex={setListIndex}
                  listIndex={listIndex}
                  favoriteIndex={favoriteIndex}
                  setFavoriteIndex={setFavoriteIndex}
                />
              )}
            </div>
          </TabPanel>
          <TabPanel>
            <div className="panel-content">
              {displayChangePassword === true ? (
                <ChangePassword
                  setDisplayChangePassword={setDisplayChangePassword}
                  setDisplayTab={() => setTabIndex(0)}
                />
              ) : (
                <ProfileSettings
                  setDisplayChangePassword={setDisplayChangePassword}
                  setDisplayTab={() => setTabIndex(0)}
                  profile={profile}
                  setFlag={setFlag}
                />
              )}
            </div>
          </TabPanel>
        </Tabs>
      </LeftBarContent>
    </>
  );
};

export default LeftBar;
